/** @jsx React.createElement */

import React from 'react';
import {storiesOf, addDecorator} from '@storybook/react';
import {withReadme} from 'storybook-readme';
import {withKnobs} from '@storybook/addon-knobs/react';

import {TJCard, TJCardSection} from '../../tj-layouts/tj-card/tj-card';
import TextReadme from '../../tj-layouts/tj-card/tj-card.md';
import {TJSectionHeading, TJSectionText} from "../tj-text-elements/tj-text-elements";
import {TJSmall, TJStrong} from "../tj-text-elements/tj-text-elements";
import {TJSticky} from "./tj-sticky";

addDecorator(withKnobs);

storiesOf('Components/Layouts', module)
  .add('"Липкая" карточка',
    withReadme(TextReadme,
      () =>
        <div className='sticky-wrapper' style={{
          height: '1000px',
          width: '100%',
          position: 'relative'
        }}>
          <TJSticky
            marginBottom={30}
            marginTop={30}
          >
            <TJCard
              header={
                <TJSectionHeading theme='cardHeading'>
                  Исключения из такс-фри
                </TJSectionHeading>
              }
            >
              <TJCardSection>
                <TJSectionText theme='cardText'>
                  Незьзя вернуть налог за покупки в интернет-магазинах, продукты из латекса, ювелирные украшения и
                  народную медицину.
                </TJSectionText>
              </TJCardSection>
              <TJCardSection>
                <TJSectionText theme='cardText'>
                  Нельзя вернуть НДС при выезде из стрены через сухопутную границу или по морю. Вернуть можно только в
                  аэропорту.
                </TJSectionText>
              </TJCardSection>
              <TJCardSection>
                <TJSectionText theme='cardText'>
                  <TJSmall>
                    <TJStrong>Сколько процентов от суммы покупки мне вернут:</TJStrong> около 7%, сумма возврата зависит
                    суммы чека.
                  </TJSmall>
                </TJSectionText>
                <TJSectionText theme='cardText'>
                  <TJSmall>
                    <TJStrong>Какая комиссия:</TJStrong> оператор возьмет комиссию в размере 100 бат.
                  </TJSmall>
                </TJSectionText>
              </TJCardSection>
            </TJCard>
          </TJSticky>
        </div>
    )
  );
