/** @jsx React.createElement */

import React from 'react';
import styles from './tj-table.m.css';

export function TJTable({headRow, children}) {

  return (
    <table className={styles.table}>
      <thead className={styles.head}>{headRow}</thead>
      <tbody className={styles.body}>{children}</tbody>
    </table>
  )
}


export function TJTableRow({children}) {

  return (
    <tr className={styles.row}>
      {children}
    </tr>
  )
}


export function TJTableHead({width, children}) {

  return (
    <th className={styles.head} width={width}>
      {children}
    </th>
  )
}

export function TJTableCell({children}) {

  return (
    <td className={styles.cell}>
      {children}
    </td>
  )
}
