/** @jsx React.createElement */

import React from 'react';
import styles from './tj-text-element.m.css';

export function TJSectionHeading({theme = 'defaultHeading', children}) {

  return (
    <h2 className={styles[theme]}>
      {children}
    </h2>
  )
}

export function TJSectionText({theme = 'defaultText', children}) {

  return (
    <p className={styles[theme]}>
      {children}
    </p>
  )
}

export function TJStrong({children}) {
  return (
    <strong className={styles.textStyleStrong}>
      {children}
    </strong>
  )
}

export function TJSmall({children}) {
  return (
    <small className={styles.textStyleSmall}>
      {children}
    </small>
  )
}

