/** @jsx React.createElement */
import React from 'react';
import {getMonthName} from "../../../common/utils";
import styles from './tj-monthpicker.m.css';
import cn from 'classnames';

export default class TJMonthpicker extends React.Component {

  constructor(props) {
    super(props);

    this._root = null;

    this.state = {
      showPicker: false,
      showDatesFrom: props.year || ((new Date()).getFullYear()),
      yearChange: false,
      monthChange: false,
    };


    this._setRootRef = (el) => {
      this._root = el
    };

    this._onYearChange = this._onYearChange.bind(this);
    this._onMonthChange = this._onMonthChange.bind(this);
    this._changeYearPeriod = this._changeYearPeriod.bind(this);
    this._togglePicker = this._togglePicker.bind(this);
    this._setRootRef = this._setRootRef.bind(this);

    this._onMobileChange = this._onMobileChange.bind(this);
    this._onResize = this._onResize.bind(this);
  }

  componentDidMount() {
    window.addEventListener('click', this.clickOutsideOfPicker);

    window.addEventListener('resize', this._onResize);
  }

  clickOutsideOfPicker = (event) => {
    if ((this.props.alwaysCustom || window.innerWidth > 800) && !this._root.contains(event.target)) {
      this.setState({showPicker: false});
    }
  };

  _onResize() {
    this.forceUpdate();
  }


  render() {
    const {
      props,
      state
    } = this;

    return (
      props.alwaysCustom || window.innerWidth > 800
        ? (
          <div
            className={styles.monthpicker}
            ref={this._setRootRef}
          >
            <button
              className={
                cn(
                  styles.input,
                  {[styles.inputExpanded] : state.showPicker}
                )
              }
              onClick={this._togglePicker}
            >
              {getMonthName(props.month, 'n', 'short').toLowerCase()} {props.year} г.
            </button>
            {state.showPicker
              ? <div className={styles.picker}>
                {this._genYearRow(state.showDatesFrom)}
                {this._genMonthTable()}
              </div>
              : ''
            }
          </div>
        )
        : (
          <input
            className={styles.pickerNative}
            type='month'
            min={this._getLimitString(this.minYear, this.minMonth)}
            max={this._getLimitString(this.maxYear, this.maxMonth)}
            onChange={this._onMobileChange}
            value={`${this._fitZeroes(props.year, 4)}-${this._fitZeroes(props.month + 1, 2)}`}
          />
        )
    );
  }


  get minYear() {
    return this.props.minYear || 0
  }

  get maxYear() {
    return this.props.maxYear || 3000
  }

  get minMonth() {
    return this.props.minMonth || 0
  }

  get maxMonth() {
    return this.props.maxMonth || 11
  }

  _getLimitString(year, month) {
    return `${this._fitZeroes(year, 4)}-${this._fitZeroes(month, 2)}`
  }

  _fitZeroes(num, len) {
    let str = num.toString();
    return '0'.repeat(len - str.length) + str
  }

  _setRootRef(el) {
    this._root = el
  }

  _onMobileChange(event: Event) {
    if (event.currentTarget instanceof HTMLInputElement) {
      if (!event.currentTarget.value) {
        if (this.props.defaultValue) {
          event.currentTarget.value = this.props.defaultValue;
          this.props.onDateChange(+this.props.defaultValue.substring(5) - 1, +this.props.defaultValue.substring(0, 4));
        }
      }
      this.props.onDateChange(+event.currentTarget.value.substring(5) - 1, +event.currentTarget.value.substring(0, 4))
    }
  }


  _onYearChange(year) {
    return () => {

      if (!this._isValidYear(year)) {
        return
      }

      let month = this.props.month;

      if (year === this.maxYear && month > this.maxMonth) {
        month = this.maxMonth
      }

      if (year === this.minYear && month < this.minMonth) {
        month = this.minMonth
      }

      this.setState({yearChange: true});

      if (this.state.monthChange) {
        this._togglePicker();
      }

      this.props.onDateChange(month, year)
    }
  }

  _onMonthChange(month) {
    return () => {

      if (!this._isValidMonth(month)) {
        return
      }


      this.setState({monthChange: true});
      if (this.state.yearChange) {
        this._togglePicker();
      }
      this.props.onDateChange(month, this.props.year)
    }
  }

  _changeYearPeriod(year, allowed) {
    return () => {
      if (!allowed) {
        return
      }
      this.setState({showDatesFrom: year});
    }
  }

  _togglePicker() {
    this.setState({
      showPicker: !this.state.showPicker,
      showDatesFrom: this.props.year,
      yearChange: false,
      monthChange: false,
    });
  }

  _genYearRow(showDatesFrom) {

    return (
      <div className={styles.yearRow}>
        <button
          className={
            cn(
              styles.prevYear,
              {[styles.prevYearHidden]: showDatesFrom <= this.minYear}
            )
          }
          onClick={this._changeYearPeriod(showDatesFrom - 4, showDatesFrom > this.minYear)}
        />

        {[0, 1, 2, 3].map(el => {

          const
            yearValue = showDatesFrom + el;

          return (
            <button
              className={
                cn(
                  styles.year,
                  {[styles.yearChosen] : this.props.year === yearValue},
                  {[styles.yearDisabled] : yearValue < this.minYear || yearValue > this.maxYear}
                )
              }
              onClick={this._onYearChange(yearValue)}
            >
              {yearValue}
            </button>
          );
        })}

        <button
          className={
            cn(
              styles.nextYear,
              {[styles.nextYearHidden]: showDatesFrom + 4 > this.maxYear}
            )
          }
          onClick={this._changeYearPeriod(showDatesFrom + 4, showDatesFrom + 4 <= this.maxYear)}
        />
      </div>
    );
  }

  _genMonthTable() {

    return (
      <div className={styles.monthTable}>
        {[0, 1, 2].map(rowNum => {
          return (
            <div className={styles.monthRow}>
              {[0, 1, 2, 3].map(el => {

                const
                  monthValue = rowNum * 4 + el;

                return (
                  <button
                    className={
                      cn(
                        styles.month,
                        {[styles.monthChosen] : this.props.month === monthValue},
                        {[styles.monthDisabled] : !this._isValidMonth(monthValue)},
                      )
                    }
                    onClick={this._onMonthChange(
                      monthValue
                    )}
                  >
                    {getMonthName(monthValue, 'n', 'short')}
                  </button>
                );
              })}
            </div>
          );
        })}
      </div>
    );
  }

  _isValidYear(year) {
    return year >= this.minYear && year <= this.maxYear
  }

  _isValidMonth(month) {
    const
      year = this.props.year;

    return (year > this.minYear && year < this.maxYear)
      || (year === this.minYear && month >= this.minMonth)
      || (year === this.maxYear && month <= this.maxMonth)
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.clickOutsideOfPicker);
    window.removeEventListener('resize', this._onResize);
  }
}
