/** @jsx React.createElement */

import React from 'react';
import cn from 'classnames';
import styles from './tj-sticky.m.css';

export class TJSticky extends React.Component {

  constructor() {
    super();

    this.state = {
      isStuck: false,
      isStuckBottom: false,
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);

    const parentNode = this.ref.parentNode;
    this.width = parentNode.offsetWidth;
    this.height = this.ref.offsetHeight;
  }

  setRef = (el) => {
    this.ref = el;
  };

  onScroll = () => {

    const {
      top: rectTop,
      bottom: rectBottom
    } = this.ref.parentNode.getBoundingClientRect();

    const
      marginBottom = this.props.marginBottom || 0,
      marginTop = this.props.marginTop || 0;

    if (
      rectTop > marginTop
      && this.state.isStuck
    ) {
      this.ref.style.width = '';
      this.ref.style.top = '';
      this.ref.style.bottom = '';
      this.setState({
        isStuck: false,
        isStuckBottom: false,
      })
    } else if (
      rectTop <= marginTop
      && rectBottom > (this.height + marginBottom + marginTop)
      && !this.state.isStuck
    ) {
      this.ref.style.width = this.width + 'px';
      this.ref.style.top = marginTop + 'px';
      this.ref.style.bottom = '';
      this.setState({
        isStuck: true,
        isStuckBottom: false,
      });
    } else if (
      rectTop <= marginTop
      && rectBottom <= (this.height + marginBottom + marginTop)
      && !this.state.isStuckBottom
    ) {
      this.ref.style.width = '';
      this.ref.style.top = '';
      this.ref.style.bottom = marginBottom + 'px';
      this.setState({
        isStuck: false,
        isStuckBottom: true,
      });
    }

  };

  render() {
    return (
      <div
        ref={this.setRef}
        className={cn(
          styles.tjSticky,
          { [styles.isStuck] : this.state.isStuck },
          { [styles.isStuckBottom] : this.state.isStuckBottom }
        )}
      >
        {this.props.children}
      </div>
    )
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  }
}
