/** @jsx React.createElement */

import React from 'react';
import TJInput from '../tj-input/tj-input.jsx';
import styles from './tj-select.m.css';
import cn from 'classnames';

export class TJSelectDefault extends React.Component {

  state = {
    expanded: false,
  };

  componentDidMount() {
    window.addEventListener('click', this.clickOutsideOfSelect);
  }


  clickOutsideOfSelect = (event) => {
    if (!this.selectContainerRef.contains(event.target)) {
      this.setState({expanded: false});
    }
  };

  setSelectContainerRef = (element) => {
    this.selectContainerRef = element
  };

  onSelect = (option) => () => {
    this.setState({expanded: false});
    this.props.onSelect(option);
  };

  onToggle = () => {
    this.setState({expanded: !this.state.expanded});
  };

  render() {

    return (
      <TJSelectExpandableContainer
        theme='selectDefault'
        containerRef={this.setSelectContainerRef}
        expanded={this.state.expanded}
        controller={
          <TJSelectControllerButton
            onToggle={this.onToggle}
            text={this.props.selected.name}
            expanded={this.state.expanded}
          />
        }
      >
        <TJSelectOptionList
          expanded={this.state.expanded}
        >
          {this.props.options.map(option => {
            return <TJSelectOption
              key={option.name}
              selected={this.props.selected.name === option.name}
              onSelect={this.onSelect(option)}
            >
              {option.name}
            </TJSelectOption>
          })}
        </TJSelectOptionList>
      </TJSelectExpandableContainer>
    )
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.clickOutsideOfSelect)
  }

}


export class TJSelectFiltered extends React.Component {

  state = {
    expanded: false,
    filter: ''
  };

  componentDidMount() {
    window.addEventListener('click', this.clickOutsideOfSelect);
  }

  clickOutsideOfSelect = (event) => {
    if (!this.selectContainerRef.contains(event.target)) {
      this.setState({expanded: false});
    }
  };

  setSelectContainerRef = (element) => {
    this.selectContainerRef = element
  };

  onSelect = (option) => () => {
    this.setState({
      expanded: false,
      filter: '',
    });

    this.props.onSelect(option);
  };

  onInputFocus = () => {
    this.setState({expanded: true});
  };

  onFilterChange = (newFilter) => {
    this.setState({filter: newFilter});
  };

  render() {

    const {
      expanded,
      filter
    } = this.state;

    const {
      selected,
      options,
      filterFunction
    } = this.props;

    return (
      <TJSelectExpandableContainer
        theme='selectFiltered'
        containerRef={this.setSelectContainerRef}
        expanded={expanded}
        controller={
          <TJInput
            theme='default'
            type='text'
            value={filter}
            placeholder={selected.name}
            onFocus={this.onInputFocus}
            onInput={this.onFilterChange}
          />
        }
      >
        <TJSelectOptionList
          expanded={expanded}
        >
          {options
            .filter(option => {
              return filterFunction
                ? filterFunction(option, filter)
                : option.name.indexOf(filter) !== -1
            })
            .map(option => {
              return <TJSelectOption
                key={option.name}
                selected={selected.name === option.name}
                onSelect={this.onSelect(option)}
              >
                {option.name}
              </TJSelectOption>
            })
          }
        </TJSelectOptionList>
      </TJSelectExpandableContainer>
    )
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.clickOutsideOfSelect)
  }

}


export class TJSelectMenu extends React.Component {

  onSelect = (option) => () => {
    this.props.onSelect(option);
  };

  render() {

    const {
      selected,
      options,
    } = this.props;

    return (
      <TJSelectMenuContainer
        theme='main'
        heading={this.props.heading}
      >
        <TJSelectOptionListWrapper
          maxHeight={this.props.maxMenuHeight}
        >
          <TJSelectOptionList
            theme='menu'
          >
            {options
              .map(option => {
                return <TJSelectOption
                  key={option.name}
                  theme='main'
                  selected={selected.name === option.name}
                  onSelect={this.onSelect(option)}
                >
                  {option.name}
                </TJSelectOption>
              })
            }
          </TJSelectOptionList>
        </TJSelectOptionListWrapper>
      </TJSelectMenuContainer>
    )
  }

}


export function TJSelectExpandableContainer({controller, theme, containerRef, children, expanded}) {

  return (
    <div
      ref={containerRef}
      className={styles[theme]}
    >
      {controller}
      {expanded
        ? children
        : ''
      }
    </div>
  )
}

export function TJSelectMenuContainer({heading, children}) {

  return (
    <div
      className={styles.menu}
    >
      <div className={styles.menuHeading}>
        {heading}
      </div>
      {children}
    </div>
  )
}


export function TJSelectControllerButton({text, onToggle}) {

  return (
    <button
      className={styles.expandButton}
      onClick={onToggle}
    >
      {text}
    </button>
  )
}


export function TJSelectOptionListWrapper({children, maxHeight}) {

  return (
    <div
      className={styles.optionListWrapper}
      style={{maxHeight: maxHeight ? maxHeight + 'px' : 'none'}}
    >
      {children}
    </div>
  )
}


export function TJSelectOptionList({children}) {

  return (
    <div
      className={styles.optionList}
    >
      {children}
    </div>
  )
}


export function TJSelectOption({selected, onSelect, children}) {


  return (
    <button
      className={
        cn(
          styles.option,
          {[styles.optionSelected] : selected}
        )
      }
      onClick={onSelect}
    >
      {children}
    </button>
  )
}


