/** @jsx React.createElement */

import React from 'react';
import styles from './tj-card.m.css';

export function TJCard({theme = 'card', children, header}) {

  return (
    <section className={styles[theme]}>
      {header}
      {children}
    </section>
  )
}

export function TJCardSection({theme = 'section', children}) {

  return (
    <section className={styles[theme]}>
      {children}
    </section>
  )
}

